* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 16px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

input {
  font: inherit;
  padding: 8px;
}

button {
  cursor: pointer;
  padding: 8px 12px;
}

.back-btn {
  text-transform: uppercase;
  color: #fff;
  background-color: #f68768;
  border: none;
  border-radius: .5rem;
  justify-content: center;
  align-items: center;
  padding: .6rem 1.5rem;
  font-size: 1rem;
  font-weight: bold;
  text-decoration: none;
  transition: transform .2s ease-out, box-shadow .2s ease-out;
  display: inline-flex;
  box-shadow: 0 2px 5px #d32525b3;
}

.back-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 6px #169bafb3;
}

.back-btn:active {
  box-shadow: none;
  transform: translateY(0);
}

.back-btn > a {
  -webkit-text-decoration: inherit;
  text-decoration: inherit;
  color: inherit;
}

.back-btn .icon {
  width: 1.6rem;
  height: 1.6rem;
  fill: inherit;
}

.page-header {
  color: #333;
  text-shadow: 0 2px 2px #0000001a;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: .12rem;
  margin-bottom: 2rem;
  font-family: Poppins, sans-serif;
  font-size: 3rem;
  font-weight: 600;
}

.page-header:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: #5f5f5f;
  margin-top: 1.5rem;
  display: block;
  box-shadow: 0 2px 2px #0000001a;
}

.content {
  max-width: 30rem;
  padding: 1.6rem;
}

.content__header {
  color: #333;
  margin-bottom: 1rem;
  padding-left: 2rem;
  font-size: 2rem;
  font-weight: bold;
}

.content__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.content__item {
  margin-bottom: .5rem;
}

.content__link {
  color: #333;
  padding: .5rem;
  font-size: 1.2rem;
  text-decoration: none;
  transition: all .2s ease-in-out;
  display: block;
  box-shadow: 2px 2px 5px #0000004d;
}

.content__link:hover {
  color: #4ae4c2;
  background-image: linear-gradient(to right, #fc354c, #0abfbc);
  transform: translateX(10px);
  box-shadow: 4px 4px 10px #00000080;
}

.footer {
  color: #333;
  text-align: center;
  margin: 2rem 0;
  font-size: .8rem;
}

.footer:before {
  content: "";
  width: 100%;
  height: 3px;
  background-color: #5f5f5f;
  margin-bottom: 3rem;
  display: block;
  box-shadow: 0 2px 2px #0000001a;
}

.footer__container {
  padding: 0 1rem;
}

.social__list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: .8rem;
  padding: 0;
  list-style: none;
  display: flex;
}

.social__item {
  width: 2.75rem;
  height: 2.75rem;
}

.social__link {
  color: #fff;
  width: 100%;
  height: 100%;
  background-color: #ffffff1a;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .3s;
  display: flex;
}

.social__link:hover, .social__link:focus {
  background-image: linear-gradient(to right, #fc354c, #0abfbc);
  transform: scale(1.2);
  box-shadow: 4px 4px 10px #00000080;
}

.social__icon {
  fill: currentColor;
  width: 45%;
  height: 45%;
}

.to-top-btn {
  width: 2.75rem;
  height: 2.75rem;
  color: #fff;
  cursor: pointer;
  background-color: #ffffff4d;
  border: none;
  border-radius: 50%;
  transition: background-color .3s ease-in-out, color .3s ease-in-out;
  position: fixed;
  bottom: 2rem;
  right: 2rem;
}

.to-top-btn:hover {
  background-image: linear-gradient(to right, #0abfbc, #fc354c);
}

.to-top-btn__icon {
  width: 100%;
  height: 100%;
