* {
  box-sizing: border-box;
}

body {
  margin: 16px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

input {
  padding: 8px;
  font: inherit;
}

button {
  padding: 8px 12px;
  cursor: pointer;
}

/* ----------------------------------------------------------- */
/* STYLES FOR BACK BTN */
.back-btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0.6rem 1.5rem;
  font-size: 1rem;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  border: none;
  border-radius: 0.5rem;
  color: #fff;
  background-color: #f68768;
  box-shadow: 0 2px 5px rgba(211, 37, 37, 0.7);
  transition: all 0.2s ease-in-out;
  transition: transform 0.2s ease-out, box-shadow 0.2s ease-out;
}

.back-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(22, 155, 175, 0.7);
}

.back-btn:active {
  transform: translateY(0);
  box-shadow: none;
}

.back-btn>a {
  text-decoration: inherit;
  color: inherit;
}

.back-btn .icon {
  width: 1.6rem;
  height: 1.6rem;
  fill: inherit;
}

/* ----------------------------------------------------------- */
/* STYLES FOR  PAGE HEADER */

.page-header {
  font-family: 'Poppins', sans-serif;
  font-size: 3rem;
  font-weight: 600;
  color: #333;
  text-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
  text-align: center;
  margin-bottom: 2rem;
  text-transform: uppercase;
  letter-spacing: 0.12rem;
}

.page-header::after {
  content: '';
  display: block;
  width: 100%;
  height: 3px;
  background-color: #5f5f5f;
  margin-top: 1.5rem;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
}

/* ----------------------------------------------------------- */
/* STYLES FOR CONTENT */

.content
{
  padding: 1.6rem;
  max-width: 30rem;
}

.content__header {
  font-size: 2rem;
  font-weight: bold;
  padding-left: 2rem;
  margin-bottom: 1rem;
  color: #333;
}

.content__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.content__item {
  margin-bottom: 0.5rem;
}

.content__link {
  display: block;
  padding: 0.5rem;
  color: #333;
  font-size: 1.2rem;
  text-decoration: none;
  transition: all 0.2s ease-in-out;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
}

.content__link:hover {
  color: #4ae4c2;
  background-image: linear-gradient(to right, #fc354c, #0abfbc);
  transform: translateX(10px);
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5);
}

/* ----------------------------------------------------------- */
/* STYLES FOR FOOTER */

.footer {
  color: #333;
  margin: 2rem 0;
  text-align: center;
  font-size: 0.8rem;
}

.footer::before {
  content: '';
  display: block;
  width: 100%;
  height: 3px;
  background-color: #5f5f5f;
  margin-bottom: 3rem;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
}

.footer__container {
  padding: 0 1rem;
}

/* ----------------------------------------------------------- */
/* STYLES FOR SOCIALS */

.social__list {
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 0.8rem;
}

.social__item {
  width: 2.75rem;
  height: 2.75rem;
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  background-color: rgba(255, 255, 255, 0.1);
  width: 100%;
  height: 100%;
  border-radius: 50%;
  transition: all 0.3s ease;
}

.social__link:hover,
.social__link:focus {
  background-image: linear-gradient(to right, #fc354c, #0abfbc);
  transform: scale(1.2);
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5);
}

.social__icon {
  fill: currentColor;
  width: 45%;
  height: 45%;
}

/* ----------------------------------------------------------- */
/* STYLES FOR BTN TO TOP */

.to-top-btn {
  position: fixed;
  right: 2rem;
  bottom: 2rem;
  width: 2.75rem;
  height: 2.75rem;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.3);
  color: #fff;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}

.to-top-btn:hover {
  background-image: linear-gradient(to right, #0abfbc, #fc354c);
}

.to-top-btn__icon {
  /* display: block; */
  /* margin: 0 auto; */
  width: 100%;
  height: 100%;
  fill: currentColor;
  transition: transform 0.3s ease-in-out;
}

.to-top-btn:hover .to-top-btn__icon {
  transform: scale(2);
}